body {
  overscroll-behavior: none;
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
}

* {
  box-sizing: border-box;
}

.logos {
  height: 50px;
  position: fixed;
  top: 5px;
  left: 5px;
}

#appContainer {
  background-color: #000;
  grid-template: "map1 map1 gallery" 500px
                 "histogram histogram gallery"
                 "timeline timeline timeline"
                 / auto auto 50%;
  gap: 10px;
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 10px;
  display: grid;
  overflow: hidden;
}

.mapLayer {
  position: absolute;
  top: 0;
  left: 0;
}

#map1, #map2 {
  position: relative;
}

#map1 {
  grid-area: map1;
}

#timeline {
  grid-area: timeline;
  width: 100vw;
  height: 100px;
}

#hist1 {
  grid-area: histogram;
  justify-content: center;
  align-items: center;
  display: flex;
}

#timeDisplay {
  font-size: large;
  position: absolute;
  top: 1em;
  left: 1em;
}

.gallery {
  flex-flow: wrap;
  grid-area: gallery;
  align-content: flex-start;
  gap: 5px;
  width: 46vw;
  height: calc(100vh - 200px);
  padding: 10px;
  display: flex;
  overflow: scroll;
}

.galleryItem {
  height: 11.5vw;
  margin: 3px;
  position: relative;
}

.galleryItem:last-of-type {
  width: 400px;
  height: 300px;
}

.galleryItem img {
  object-fit: cover;
  width: 100%;
  height: 80%;
}

.galleryItem .credits {
  color: #000;
  font-size: small;
  position: absolute;
  top: 2px;
  right: 2px;
}

.galleryItem .caption {
  background-color: #000;
  width: 100%;
  font-size: medium;
}

